import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
import getters from './getters'
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex);

export const initialState = () => ({
  userInfo: null, // 
  token: null, // 
  emailForResetPassword: null, // 
  isResetPassword: false, // 
  services: [], // services
  environments: [], // environments
  tests: [], // testcases
  steps: null, // testcasesetup/teardown
  setups: [], // setups
  teardowns: [], // teardowns
  assertions: [], // assertion
  env: 'All', //
  results: [], // case result
  runs: [], // suite result
  schedules: [], // 
  hooks: [] // hook
})

const state = initialState;
const vuexLocalStorage = createPersistedState({
  reducer: state => {
    const stateFilter = Object.assign({}, state);
    const blackList = ['env', 'services', 'environments', 'tests', 'steps', 'setups', 'teardowns', 'assertions', 'results', 'runs'];
    blackList.forEach((item) => {
      delete stateFilter[item]
    })
    return stateFilter;
  }
});

export default new Vuex.Store({
  state,
  actions,
  mutations,
  getters,
  plugins: [vuexLocalStorage]
})



